import { Injectable } from '@angular/core';
import { Store, select } from '@ngrx/store';
import { Observable } from 'rxjs';

import * as GeoActions from './geo.actions';
import { GeoCoordinates } from '../../models/geo-oordinates.model';
import {
	selectGeoCoordinates,
	selectGeoCoordinatesIsLoading,
	selectGeoLocation,
} from './geo.selectors';

@Injectable({
	providedIn: 'root',
})
export class GeoFacade {
	constructor(private store: Store) {}

	public getCoordinates(): void {
		this.store.dispatch(GeoActions.getCoordinatesByBrowser({}));
	}

	public setCoordinates(address: any): void {
		const coordinates: GeoCoordinates = {
			latitude:
				(address.geometry.viewport.Za.i +
					address.geometry.viewport.Za.j) /
				2,
			longitude:
				(address.geometry.viewport.Ua.i +
					address.geometry.viewport.Ua.j) /
				2,
		};

		this.store.dispatch(
			GeoActions.setCoordinatesByInputSelectedLocation({
				coordinates,
			})
		);
	}

	public setLocation(address: any): void {
		const location: string = address.formatted_address;

		this.store.dispatch(
			GeoActions.setLocationByInputSelectedLocation({
				location,
			})
		);
	}

	public selectGeoCoordinates(): Observable<GeoCoordinates> {
		return this.store.pipe(select(selectGeoCoordinates));
	}

	public selectGeoCoordinatesIsLoading(): Observable<boolean> {
		return this.store.pipe(select(selectGeoCoordinatesIsLoading));
	}

	public selectGeoLocation(): Observable<string> {
		return this.store.pipe(select(selectGeoLocation));
	}
}
