import { Injectable, RendererFactory2, Inject, Renderer2 } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable, Subscriber } from 'rxjs';
import { DOCUMENT } from '@angular/common';

import { selectCurrentTheme } from '../store/user/user.selectors';

@Injectable({
	providedIn: 'root',
})
export class ThemeService {
	private LocalStorageKey: string = 'Selected Theme';
	private renderer: Renderer2;

	constructor(
		@Inject(DOCUMENT) private document: Document,
		private rendererFactory: RendererFactory2,
		private store: Store
	) {
		this.renderer = this.rendererFactory.createRenderer(null, null);
	}

	public saveThemeToLocalStorage(): void {
		this.store
			.select(selectCurrentTheme)
			.subscribe((theme: string) =>
				localStorage.setItem(
					this.LocalStorageKey,
					JSON.stringify(theme)
				)
			);
	}

	public setTheme(): void {
		this.store.select(selectCurrentTheme).subscribe((theme: string) => {
			if (theme === 'dark') {
				this.renderer.removeClass(
					this.document.documentElement,
					'light'
				);
			} else {
				this.renderer.removeClass(
					this.document.documentElement,
					'dark'
				);
			}
			this.renderer.addClass(this.document.documentElement, theme);
			this.renderer.addClass(this.document.documentElement, 'transition');
			const TRANSITION_REMOVE_DELAY: number = 500;
			setTimeout(() => {
				this.renderer.removeClass(
					this.document.documentElement,
					'transition'
				);
			}, TRANSITION_REMOVE_DELAY);
			this.saveThemeToLocalStorage();
		});
	}

	public getThemeFromLocalStorage(): Observable<string> {
		return new Observable((observer: Subscriber<string>) => {
			const LocalStorageData: string = localStorage.getItem(
				this.LocalStorageKey
			);
			if (Boolean(LocalStorageData) || LocalStorageData !== undefined) {
				observer.next(JSON.parse(LocalStorageData));
			} else {
				observer.next('light');
			}
		});
	}
}
