import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, Subscriber, combineLatest } from 'rxjs';

import { GOOGLE_API_KEY } from './../api/google.apikey';

import { GeoCoordinates } from '../models/geo-oordinates.model';
import { GeoFacade } from '../store/geo/geo.facade';
import { UserFacade } from '../store/user';
import { RouterFacade } from '../store/router/router.facade';

@Injectable({
	providedIn: 'root',
})
export class GeoService {
	constructor(
		private http: HttpClient,
		private geoFacade: GeoFacade,
		private userFacade: UserFacade,
		private routerFacade: RouterFacade
	) {}

	public getCoordinatesByBrowser(): Observable<GeoCoordinates> {
		return new Observable((observer: Subscriber<GeoCoordinates>) => {
			if (navigator.geolocation) {
				navigator.geolocation.getCurrentPosition(
					(position: Position) => {
						observer.next({
							latitude: position.coords.latitude,
							longitude: position.coords.longitude,
						});
					},
					(error: PositionError) => {
						observer.error(error);
					}
				);
			} else {
				observer.error('Geolocation not available');
			}
		});
	}

	public getCoordinatesByAPI(): Observable<GeoCoordinates> {
		return new Observable((observer: Subscriber<GeoCoordinates>) => {
			this.http
				.get('https://ipapi.co/json/')
				.subscribe((data: GeoCoordinates) => {
					if (data) {
						const coordinates: GeoCoordinates = {
							latitude: data.latitude,
							longitude: data.longitude,
						};

						observer.next(coordinates);
					} else {
						observer.next(null);
					}
				});
		});
	}

	public getLocationByCoordinates(): Observable<any> {
		return new Observable((observer: Subscriber<GeoCoordinates>) => {
			combineLatest([
				this.geoFacade.selectGeoCoordinates(),
				this.userFacade.selectCurrentLanguage(),
			])
				.subscribe(([coordinates, lang]: [GeoCoordinates, string]) => {
					this.http
						.get(
							`https://maps.googleapis.com/maps/api/geocode/json?latlng=${coordinates.latitude},${coordinates.longitude}&result_type=locality&language=${lang}&key=${GOOGLE_API_KEY}`
						)
						.subscribe((data: any) => {
							const SUBSTRING_NUMBER: number = 8;
							if (
								data.plus_code !== undefined ||
								data.plus_code.compound_code !== undefined
							) {
								observer.next(
									data.plus_code.compound_code.substring(
										SUBSTRING_NUMBER
									)
								);
							} else {
								observer.next(null);
							}
						});
				})
				.unsubscribe();
		});
	}

	public getLocationByCoordinatesFromQueryParams(): Observable<any> {
		return new Observable((observer: Subscriber<any>) => {
			combineLatest([
				this.routerFacade.selectQueryParams(),
				this.userFacade.selectCurrentLanguage(),
			])
				.subscribe(([coordinates, lang]: [any, string]) => {
					this.http
						.get(
							`https://maps.googleapis.com/maps/api/geocode/json?latlng=${coordinates.ltd},${coordinates.lng}&result_type=locality&language=${lang}&key=${GOOGLE_API_KEY}`
						)
						.subscribe((data: any) => {
							const SUBSTRING_NUMBER: number = 8;
							if (
								data.plus_code &&
								data.plus_code.compound_code
							) {
								observer.next(
									data.plus_code.compound_code.substring(
										SUBSTRING_NUMBER
									)
								);
							} else {
								observer.next('No city detected');
							}
						});
				})
				.unsubscribe();
		});
	}

	public getTimeZoneByCoordinatesFromQueryParams(): Observable<number> {
		return new Observable((observer: Subscriber<number>) => {
			this.routerFacade
				.selectQueryParams()
				.subscribe((coordinates: any) => {
					this.http
						.get(
							`https://maps.googleapis.com/maps/api/timezone/json?location=${
								coordinates.ltd
							},${coordinates.lng}&timestamp=${Math.floor(
								new Date().getTime() / 1000
							)}&key=${GOOGLE_API_KEY}
							`
						)
						.subscribe((data: any) => {
							observer.next(data.rawOffset / 3600);
						});
				})
				.unsubscribe();
		});
	}
}
