import { Injectable } from '@angular/core';
import { Store, select } from '@ngrx/store';
import { Observable } from 'rxjs';

import * as UserActions from './user.actions';
import { LanguageService } from '../../services/language.service';
import {
	selectCurrentLanguage,
	selectCurrentTheme,
	selectShowTooltip,
} from './user.selectors';
import { ThemeService } from '../../services/theme.service';

@Injectable({
	providedIn: 'root',
})
export class UserFacade {
	constructor(
		private store: Store,
		private languageService: LanguageService,
		private themeService: ThemeService
	) {}

	public initLanguage(): void {
		this.languageService
			.getLanguageFromLocalStorage()
			.subscribe((language: string) => {
				if (language === 'ru') {
					this.store.dispatch(UserActions.setLanguageRu({}));
				}
			});
		this.languageService.saveLanguageToLocalStorage();
	}

	public setLanguageEn(): void {
		this.selectCurrentLanguage()
			.subscribe((language: string) => {
				if (language !== 'en') {
					this.store.dispatch(UserActions.setLanguageEn({}));
				}
			})
			.unsubscribe();
	}

	public setLanguageRu(): void {
		this.selectCurrentLanguage()
			.subscribe((language: string) => {
				if (language !== 'ru') {
					this.store.dispatch(UserActions.setLanguageRu({}));
				}
			})
			.unsubscribe();
	}

	public selectCurrentLanguage(): Observable<string> {
		return this.store.pipe(select(selectCurrentLanguage));
	}

	public initTheme(): void {
		this.themeService
			.getThemeFromLocalStorage()
			.subscribe((theme: string) => {
				if (theme === 'dark') {
					this.store.dispatch(UserActions.setThemeDark({}));
				}
				this.themeService.setTheme();
			});
	}

	public setThemeLight(): void {
		this.selectCurrentTheme()
			.subscribe((theme: string) => {
				if (theme !== 'light') {
					this.store.dispatch(UserActions.setThemeLight({}));
				}
			})
			.unsubscribe();
	}

	public setThemeDark(): void {
		this.selectCurrentTheme()
			.subscribe((theme: string) => {
				if (theme !== 'dark') {
					this.store.dispatch(UserActions.setThemeDark({}));
				}
			})
			.unsubscribe();
	}

	public tooltipShown(): void {
		this.selectShowTooltip()
			.subscribe((show: boolean) => {
				if (show === true) {
					this.store.dispatch(UserActions.tooltipShown({}));
				}
			})
			.unsubscribe();
	}

	public selectCurrentTheme(): Observable<string> {
		return this.store.pipe(select(selectCurrentTheme));
	}

	public selectShowTooltip(): Observable<boolean> {
		return this.store.pipe(select(selectShowTooltip));
	}
}
