import { Injectable } from '@angular/core';
import { Store, select } from '@ngrx/store';

import * as AirActions from './air.actions';
import { AirData } from '../../models/air-data.model';
import { selectAirData } from './air.selectors';
import { Observable } from 'rxjs';

@Injectable({
	providedIn: 'root',
})
export class AirFacade {
	constructor(private store: Store) {}

	public getAirDataByApi(): void {
		this.store.dispatch(AirActions.getAirDataByApi({}));
	}

	public selectAirData(): Observable<AirData> {
		return this.store.pipe(select(selectAirData));
	}
}
