import { Directive, Input, ElementRef, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { UserFacade } from '../store/user';

@Directive({
	selector: '[background]',
})
export class BackgroundDirective implements OnInit, OnDestroy {
	@Input('background') private name: string;

	private SubGetTheme: Subscription;

	constructor(
		private elementRef: ElementRef,
		private userFacade: UserFacade
	) {}

	public ngOnInit(): void {
		this.SubGetTheme = this.userFacade
			.selectCurrentTheme()
			.subscribe((theme: string) => {
				if (this.name === 'sky') {
					this.elementRef.nativeElement.classList.remove(`light`);
					this.elementRef.nativeElement.classList.remove(`dark`);
					this.elementRef.nativeElement.classList.add(`${theme}`);
				} else {
					this.elementRef.nativeElement.innerHTML = `<img  style="width:192rem;height: 108rem"  src="../../../../assets/background/${theme}/${this.name}.png" />`;
				}
			});
	}

	public ngOnDestroy(): void {
		this.SubGetTheme.unsubscribe();
	}
}
