import {
	createFeatureSelector,
	createSelector,
	MemoizedSelector,
	MemoizedSelectorWithProps,
} from '@ngrx/store';

import { IAppState, FeatureKey } from '../index';
import { IGeoState } from './geo.state';
import { GeoCoordinates } from '../../models/geo-oordinates.model';

export const selectGeoState: MemoizedSelector<
	IAppState,
	IGeoState
> = createFeatureSelector<IAppState, IGeoState>(FeatureKey.Geo);

export const selectGeoCoordinates: MemoizedSelector<
	IAppState,
	GeoCoordinates
> = createSelector(selectGeoState, (state: IGeoState) => state.oordinates);

export const selectGeoLocation: MemoizedSelector<
	IAppState,
	string
> = createSelector(selectGeoState, (state: IGeoState) => state.location);

export const selectGeoCoordinatesIsLoading: MemoizedSelector<
	IAppState,
	boolean
> = createSelector(selectGeoState, (state: IGeoState) => state.isLoading);

