import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AsyncSubject } from 'rxjs';

import { Languages, Files } from 'src/assets/languages';

@Injectable({
	providedIn: 'root',
})
export class TranslationService {
	private cacheable: any = {};
	private subject: AsyncSubject<any> = new AsyncSubject();

	constructor(private http: HttpClient) {
		let i: number = 1;

		for (const lang of Languages) {
			this.cacheable[lang] = {};
			for (const file of Files) {
				this.http
					.get(`assets/languages/${lang}/${file}.json`)
					.subscribe((data: any) => {
						this.cacheable[lang][file] = data;
						this.subject.next(this.cacheable);
						if (i === Files.length * 2) {
							this.subject.complete();
						} else {
							i++;
						}
					});
			}
		}
	}

	public get(): AsyncSubject<any> {
		return this.subject;
	}
}
