import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { UserFacade } from './store/user/user.facade';
import { GeoFacade } from './store/geo/geo.facade';
import { DOCUMENT } from '@angular/common';
import { Subscription } from 'rxjs';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit, OnDestroy {
	private subSelectCurrentTheme: Subscription;

	constructor(
		@Inject(DOCUMENT) private document: Document,
		private userFacade: UserFacade,
		private geoFacade: GeoFacade
	) {}

	public ngOnInit(): void {
		this.geoFacade.getCoordinates();

		this.userFacade.initLanguage();
		this.userFacade.initTheme();

		this.subSelectCurrentTheme = this.userFacade
			.selectCurrentTheme()
			.subscribe((theme: string) => {
				const chrome: Element = this.document.head.querySelector(
					'meta[name=theme-color]'
				);
				const safari: Element = this.document.head.querySelector(
					'meta[name=apple-mobile-web-app-status-bar-style]'
				);

				if (theme === 'light') {
					chrome.setAttribute('content', '#ffffff');
					safari.setAttribute('content', '#ffffff');
				} else if (theme === 'dark') {
					chrome.setAttribute('content', '#0f0f0f');
					safari.setAttribute('content', '#0f0f0f');
				}
			});
	}

	public ngOnDestroy(): void {
		this.subSelectCurrentTheme.unsubscribe();
	}
}
