import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { WEATHERBIT_API_KEY } from './../api/weatherbit.apikey';

@Injectable({
	providedIn: 'root',
})
export class AirService {
	constructor(private http: HttpClient) {}

	public getAirDataByApi(
		latitude: string,
		longitude: string
	): Observable<object> {
		return this.http.get(
			`https://api.weatherbit.io/v2.0/current/airquality?lat=${latitude}&lon=${longitude}&key=${WEATHERBIT_API_KEY}`
		);
	}
}
