import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError, map, withLatestFrom, mergeMap } from 'rxjs/operators';

import { Action } from '@ngrx/store';
import { Actions, createEffect, ofType } from '@ngrx/effects';

import * as WeatherActions from './weather.actions';
import { RouterFacade } from '../router';
import { Params } from '@angular/router';
import { TypedAction } from '@ngrx/store/src/models';
import { WeatherService } from 'src/app/services/weather.service';
import { WeatherbitWeather } from '../../models/weatherbit.model';

@Injectable()
export class WeatherEffects {
	public getWeatherDataByApi$: Observable<Action> = createEffect(() =>
		this.action$.pipe(
			ofType(WeatherActions.getWeatherDataByApi),
			withLatestFrom(this.routerFacade.selectQueryParams()),
			mergeMap(
				([, coordinates]: [object & TypedAction<string>, Params]) =>
					this.weatherService
						.getWeatherDataByApi(coordinates.ltd, coordinates.lng)
						.pipe(
							map((weatherData: WeatherbitWeather) => {
								return WeatherActions.getWeatherDataSuccess({
									weatherData: weatherData.data[0],
								});
							}),
							catchError((error: any) => {
								return of(
									WeatherActions.getWeatherDataError({
										error,
									})
								);
							})
						)
			)
		)
	);

	constructor(
		private action$: Actions,
		private weatherService: WeatherService,
		private routerFacade: RouterFacade
	) {}
}
