import { RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Params } from '@angular/router';
import * as fromRouter from '@ngrx/router-store';

export interface IRouterState {
	url: string;
	queryParams: Params;
	params: Params;
}

export class CustomSerializer
	implements fromRouter.RouterStateSerializer<IRouterState> {
	public serialize(routerState: RouterStateSnapshot): IRouterState {
		const { url }: RouterStateSnapshot = routerState;
		const { queryParams }: Params = routerState.root;

		let state: ActivatedRouteSnapshot = routerState.root;
		while (state.firstChild) {
			state = state.firstChild;
		}

		const { params }: any = state.params;

		return { url, queryParams, params };
	}
}
