import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError, map, mergeMap } from 'rxjs/operators';

import { Action } from '@ngrx/store';
import { Actions, createEffect, ofType } from '@ngrx/effects';

import * as GeoActions from './geo.actions';
import { GeoService } from '../../services/geo.service';
import { GeoCoordinates } from 'src/app/models/geo-oordinates.model';

@Injectable()
export class GeoEffects {
	public getCoordinatesByBrowser$: Observable<Action> = createEffect(() =>
		this.action$.pipe(
			ofType(GeoActions.getCoordinatesByBrowser),
			mergeMap(() =>
				this.geoService.getCoordinatesByBrowser().pipe(
					map((coordinates: GeoCoordinates) => {
						return GeoActions.getCoordinatesSuccess({
							coordinates,
						});
					}),
					catchError((error: PositionError) => [
						GeoActions.getCoordinatesByBrowserDeclined({}),
						GeoActions.getCoordinatesByApi({}),
					])
				)
			)
		)
	);

	public getCoordinatesByApi$: Observable<Action> = createEffect(() =>
		this.action$.pipe(
			ofType(GeoActions.getCoordinatesByApi),
			mergeMap(() =>
				this.geoService.getCoordinatesByAPI().pipe(
					map((coordinates: GeoCoordinates) => {
						return GeoActions.getCoordinatesSuccess({
							coordinates,
						});
					}),
					catchError((error: PositionError) => {
						return of(GeoActions.getCoordinatesError({ error }));
					})
				)
			)
		)
	);

	public ActivateGetLocationByCoordinates$: Observable<Action> = createEffect(
		() =>
			this.action$.pipe(
				ofType(GeoActions.getCoordinatesSuccess),
				map(() => {
					return GeoActions.getLocationByCoordinates({});
				})
			)
	);

	public getLocationByCoordinates$: Observable<Action> = createEffect(() =>
		this.action$.pipe(
			ofType(GeoActions.getLocationByCoordinates),
			mergeMap(() =>
				this.geoService.getLocationByCoordinates().pipe(
					map((data: string) => {
						return GeoActions.getLocationByCoordinatesSuccess({
							location: data,
						});
					}),
					catchError((error: PositionError) => {
						return of(
							GeoActions.getLocationByCoordinatesError({ error })
						);
					})
				)
			)
		)
	);

	constructor(private action$: Actions, private geoService: GeoService) {}
}
