import {
	createFeatureSelector,
	MemoizedSelector,
	createSelector,
} from '@ngrx/store';

import { IAppState, FeatureKey } from '../index';
import { IAirState } from './air.state';
import { AirData } from '../../models/air-data.model';

export const selectAirState: MemoizedSelector<
	IAppState,
	IAirState
> = createFeatureSelector<IAppState, IAirState>(FeatureKey.Air);

export const selectAirData: MemoizedSelector<
	IAppState,
	AirData
> = createSelector(selectAirState, (state: IAirState) => state);
