import {
	Component,
	OnInit,
	OnDestroy,
	ElementRef,
	ViewChild,
} from '@angular/core';
import { combineLatest, Subscription } from 'rxjs';
import { Router } from '@angular/router';

import { UserFacade } from '../../store/user/user.facade';

import { MusicService } from '../music/music.service';

@Component({
	selector: 'app-header',
	templateUrl: './header.component.html',
	styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit, OnDestroy {
	private subUserFacade: Subscription;
	private subSelectShowTooltip: Subscription;

	public tooltipPlayer: boolean = false;
	public tooltipShuffle: boolean = false;

	public language: string;
	public theme: string;
	public volume: number;

	constructor(
		private router: Router,
		private userFacade: UserFacade,
		private musicService: MusicService
	) {}

	public ngOnInit(): void {
		this.subUserFacade = combineLatest([
			this.userFacade.selectCurrentLanguage(),
			this.userFacade.selectCurrentTheme(),
		]).subscribe(([language, theme]: string[]) => {
			this.language = language;
			this.theme = theme;
		});

		this.musicService.getVolume().subscribe((volume: number) => {
			this.volume = volume;
		});

		this.subSelectShowTooltip = this.userFacade
			.selectShowTooltip()
			.subscribe((show: boolean) => {
				if (show === true) {
					setTimeout(() => {
						this.tooltipPlayer = true;
					}, 1000);

					setTimeout(() => {
						this.tooltipPlayer = false;
					}, 3000);

					setTimeout(() => {
						this.tooltipShuffle = true;
					}, 3300);

					setTimeout(() => {
						this.tooltipShuffle = false;
						this.userFacade.tooltipShown();
					}, 5300);
				}
			});
	}

	public ngOnDestroy(): void {
		this.subUserFacade.unsubscribe();
		this.subSelectShowTooltip.unsubscribe();
	}

	public navigateToHome(): void {
		this.router.navigateByUrl('/');
	}

	public languageToggle(): void {
		if (this.language === 'en') {
			this.userFacade.setLanguageRu();
		} else if (this.language === 'ru') {
			this.userFacade.setLanguageEn();
		}
	}

	public themeToggle(): void {
		if (this.theme === 'light') {
			this.userFacade.setThemeDark();
		} else if (this.theme === 'dark') {
			this.userFacade.setThemeLight();
		}
	}

	public onVolumeChange(value: number): void {
		this.musicService.setVolume(value);
	}

	public shuffle(): void {
		this.musicService.nextSong();
	}
}
