import { Directive, Input, ElementRef, OnInit, OnDestroy } from '@angular/core';

import { Subscription, combineLatest } from 'rxjs';
import { TranslationService } from '../services/translation.service';
import { UserFacade } from '../store/user';

@Directive({
	selector: '[translate]',
})
export class TranslationDirective implements OnInit, OnDestroy {
	@Input() private translate: [string, string];

	private SubGetCache: Subscription;

	constructor(
		private elementRef: ElementRef,
		private cacheable: TranslationService,
		private userFacade: UserFacade
	) {}

	public ngOnInit(): void {
		this.SubGetCache = combineLatest([
			this.userFacade.selectCurrentLanguage(),
			this.cacheable.get(),
		]).subscribe(([language, cacheable]: [string, any]) => {
			if (cacheable[language][this.translate[0]]) {
				this.elementRef.nativeElement.innerHTML =
					cacheable[language][this.translate[0]][this.translate[1]];
			}
		});
	}

	public ngOnDestroy(): void {
		this.SubGetCache.unsubscribe();
	}
}
