import { environment } from '../environments/environment';
import { reducers, effects } from './store';
import { GOOGLE_API_KEY } from './api/google.apikey';

import { NgModule, ErrorHandler } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { BrowserModule } from '@angular/platform-browser';

import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import {
	StoreRouterConnectingModule,
	RouterStateSerializer,
} from '@ngrx/router-store';

import { AgmCoreModule } from '@agm/core';

import { HomePageModule } from './components/home-page/home-page.module';
import { InfoPageModule } from './components/info-page/info-page.module';
import { ErrorPageModule } from './components/error-page/error-page.module';
import { MusicModule } from './components/music/music.module';

import { AppComponent } from './app.component';
import { CustomSerializer } from './store/router/router.state';

import { registerLocaleData } from '@angular/common';
import localeRu from '@angular/common/locales/ru';

import { GlobalErrorHandler } from './global-error-handler';

registerLocaleData(localeRu, 'ru');

@NgModule({
	declarations: [AppComponent],
	imports: [
		BrowserModule,
		AppRoutingModule,

		AgmCoreModule.forRoot({
			apiKey: GOOGLE_API_KEY,
		}),
		StoreModule.forRoot(reducers),
		StoreDevtoolsModule.instrument({
			maxAge: 25,
			logOnly: environment.production,
		}),
		EffectsModule.forRoot(effects),
		StoreRouterConnectingModule.forRoot(),

		HomePageModule,
		InfoPageModule,
		ErrorPageModule,
		MusicModule,
	],
	providers: [
		{ provide: RouterStateSerializer, useClass: CustomSerializer },
		{
			provide: ErrorHandler,
			useClass: GlobalErrorHandler,
		},
	],
	bootstrap: [AppComponent],
})
export class AppModule {}
