export interface IWeatherState {
	temperature: number;
	apparent_temperature: number;
	relative_humidity: number;
	pressure: number;
	ultraviolet: number;
	wind_speed: number;
	weather: {
		icon: string;
		code: string;
		description: string;
	};
	sunrise: string;
	sunset: string;
	clouds: number;
	part_of_the_day: string;
	isLoading: boolean;
}

