import {
	createFeatureSelector,
	createSelector,
	MemoizedSelector,
	DefaultProjectorFn,
} from '@ngrx/store';

import { Params } from '@angular/router';
import { FeatureKey } from '..';

import * as fromRouter from '@ngrx/router-store';
import { IRouterState } from './router.state';

export const selectRouterFull: MemoizedSelector<
	object,
	fromRouter.RouterReducerState<IRouterState>,
	DefaultProjectorFn<fromRouter.RouterReducerState<IRouterState>>
> = createFeatureSelector<fromRouter.RouterReducerState<IRouterState>>(
	FeatureKey.Router
);

export const selectRouterState: MemoizedSelector<
	object,
	IRouterState,
	DefaultProjectorFn<IRouterState>
> = createSelector<
	object,
	fromRouter.RouterReducerState<IRouterState>,
	IRouterState
>(
	selectRouterFull,
	(full: fromRouter.RouterReducerState<IRouterState>) => full?.state
);

export const selectQueryParams: MemoizedSelector<
	object,
	Params,
	DefaultProjectorFn<Params>
> = createSelector<object, IRouterState, Params>(
	selectRouterState,
	(state: IRouterState) => state?.queryParams
);
