import { createAction, props } from '@ngrx/store';
import { Action } from '../action-type';
import { GeoCoordinates } from '../../models/geo-oordinates.model';

export const getCoordinatesByBrowser: Action<string> = createAction(
	'[GEO] Using Browser to get coordinates...'
);

export const getCoordinatesByBrowserDeclined: Action<string> = createAction(
	'[GEO] User has declined a browser request.'
);

export const getCoordinatesByApi: Action<string> = createAction(
	'[GEO] Using API to get coordinates...'
);

export const getCoordinatesSuccess: Action<
	string,
	{ coordinates: GeoCoordinates }
> = createAction(
	'[GEO] Coordinates requset success!',
	props<{ coordinates: GeoCoordinates }>()
);

export const getCoordinatesError: Action<
	string,
	{ error: PositionError }
> = createAction(
	'[GEO] Coordinates requset error!',
	props<{ error: PositionError }>()
);

export const getLocationByCoordinates: Action<string> = createAction(
	'[GEO] Using Reverse Geocoding to get location...'
);

export const getLocationByCoordinatesSuccess: Action<
	string,
	{ location: string }
> = createAction(
	'[GEO] Location requset success!',
	props<{ location: string }>()
);

export const getLocationByCoordinatesError: Action<
	string,
	{ error: any }
> = createAction('[GEO] Location requset error!', props<{ error: any }>());

export const setCoordinatesByInputSelectedLocation: Action<
	string,
	{ coordinates: GeoCoordinates }
> = createAction(
	'[GEO] Set coordinates by input selected location.',
	props<{ coordinates: GeoCoordinates }>()
);

export const setLocationByInputSelectedLocation: Action<
	string,
	{ location: string }
> = createAction(
	'[GEO] Set location by input selected location.',
	props<{ location: string }>()
);
