import { Component, OnInit, OnDestroy } from '@angular/core';
import * as CanvasJS from '../../../../assets/canvasJs/canvasjs.min.js';
import { Subscription, combineLatest } from 'rxjs';
import { AirFacade } from 'src/app/store/air/index.js';
import { AirData } from '../../../models/air-data.model';
import { UserFacade } from '../../../store/user/user.facade';

@Component({
	selector: 'app-chart',
	templateUrl: './chart.component.html',
	styleUrls: ['./chart.component.scss'],
})
export class ChartComponent implements OnInit, OnDestroy {
	private subSelectAirData: Subscription;

	constructor(private airFacade: AirFacade, private userFacade: UserFacade) {}

	public ngOnInit(): void {
		this.subSelectAirData = combineLatest([
			this.airFacade.selectAirData(),
			this.userFacade.selectCurrentTheme(),
			this.userFacade.selectCurrentLanguage(),
		]).subscribe(([airData, theme, lang]: [AirData, string, string]) => {
			let unit: string;
			let interlacedColor: string;
			let gridColor: string;
			let color: string;
			let fillOpacity: number;

			if (lang === 'en') {
				unit = 'g/m';
			} else if (lang === 'ru') {
				unit = '/';
			}

			if (theme === 'light') {
				interlacedColor = ' rgba(85, 102, 107, 0.2)';
				gridColor = 'rgba(80, 92, 95, 0.1)';
				color = '#085581';
				fillOpacity = 0.7;
			} else if (theme === 'dark') {
				interlacedColor = 'rgba(3, 53, 93, 0.15)';
				gridColor = 'rgba(1,77,101,.1)';
				color = '#0072cf';
				fillOpacity = 0.95;
			}

			const chart: any = new CanvasJS.Chart('chartContainer', {
				animationEnabled: true,
				theme: `${theme}1`,
				backgroundColor: 'transparent',
				axisX: {
					interval: 1,
				},
				axisY2: {
					interlacedColor: `${interlacedColor}`,
					gridColor: `${gridColor}`,
				},
				data: [
					{
						type: 'bar',
						name: 'air',
						indexLabel: `{y} ${unit}`,
						axisYType: 'secondary',
						color: `${color}`,
						fillOpacity: `${fillOpacity}`,
						dataPoints: [
							{ y: airData.pm25, label: 'pm25' },
							{ y: airData.pm10, label: 'pm10' },
							{ y: airData.so2, label: 'S02' },
							{ y: airData.no2, label: 'NO2' },
							{ y: airData.o3, label: '03' },
							{ y: airData.co, label: 'CO' },
						],
					},
				],
			});
			chart.render();
		});
	}

	public ngOnDestroy(): void {
		this.subSelectAirData.unsubscribe();
	}
}
