import { Injectable } from '@angular/core';
import { Observable, Subscriber, of } from 'rxjs';

import { Store } from '@ngrx/store';
import { selectCurrentLanguage } from '../store/user/user.selectors';

@Injectable({
	providedIn: 'root',
})
export class LanguageService {
	private LocalStorageKey: string = 'Selected Language';

	constructor(private store: Store) {}

	public saveLanguageToLocalStorage(): void {
		this.store
			.select(selectCurrentLanguage)
			.subscribe((language: string) =>
				localStorage.setItem(
					this.LocalStorageKey,
					JSON.stringify(language)
				)
			);
	}

	public getLanguageFromLocalStorage(): Observable<string> {
		return new Observable((observer: Subscriber<string>) => {
			const LocalStorageData: string = localStorage.getItem(
				this.LocalStorageKey
			);
			if (Boolean(LocalStorageData)) {
				observer.next(JSON.parse(LocalStorageData));
			} else {
				observer.next('en');
			}
		});
	}
}
