import { Component, OnInit, OnDestroy } from '@angular/core';
import { WeatherFacade } from '../../../store/weather/weather.facade';
import { IWeatherState } from '../../../store/weather/weather.state';
import { Subscription } from 'rxjs';
import { GeoService } from '../../../services/geo.service';

@Component({
	selector: 'app-weather',
	templateUrl: './weather.component.html',
	styleUrls: ['./weather.component.scss'],
})
export class WeatherComponent implements OnInit, OnDestroy {
	private subSelectWeatherData: Subscription;

	public weatherData: IWeatherState;

	public sunriseSunset: string;

	constructor(
		private weatherFacade: WeatherFacade,
		private geoService: GeoService
	) {}

	public ngOnInit(): void {
		this.subSelectWeatherData = this.weatherFacade
			.selectWeatherData()
			.subscribe((weatherData: IWeatherState) => {
				if (weatherData && weatherData !== null) {
					this.weatherData = weatherData;
					if (
						weatherData.sunrise !== null &&
						weatherData.sunset !== null
					) {
						this.geoService
							.getTimeZoneByCoordinatesFromQueryParams()
							.subscribe((timezone: number) => {
								const sunriseWithUTS: any = this.weatherData.sunrise.split(
									':'
								);
								const sunsetWithUTS: any = this.weatherData.sunset.split(
									':'
								);
								sunriseWithUTS[0] =
									Number(sunriseWithUTS[0]) + timezone;
								sunsetWithUTS[0] =
									Number(sunsetWithUTS[0]) + timezone;

								if (sunriseWithUTS[0] >= 24) {
									sunriseWithUTS[0] -= 24;
								}

								if (sunsetWithUTS[0] >= 24) {
									sunsetWithUTS[0] -= 24;
								}

								this.sunriseSunset =
									sunriseWithUTS[0] +
									':' +
									sunriseWithUTS[1] +
									' / ' +
									sunsetWithUTS[0] +
									':' +
									sunsetWithUTS[1];
							});
					}
				}
			});
	}

	public ngOnDestroy(): void {
		this.subSelectWeatherData.unsubscribe();
	}
}
