import {
	createFeatureSelector,
	MemoizedSelector,
	createSelector,
	DefaultProjectorFn,
} from '@ngrx/store';

import { IAppState, FeatureKey } from '../index';
import { IWeatherState } from './weather.state';

export const selectWeatherState: MemoizedSelector<
	IAppState,
	IWeatherState
> = createFeatureSelector<IAppState, IWeatherState>(FeatureKey.Weather);

export const selectWeatherData: MemoizedSelector<
	IAppState,
	IWeatherState
> = createSelector(selectWeatherState, (state: IWeatherState) => state);

export const selectWeatherIcon: MemoizedSelector<
	IAppState,
	string,
	DefaultProjectorFn<string>
> = createSelector(
	selectWeatherState,
	(state: IWeatherState) => state.weather.icon
);
