import { createReducer, on, Action, ActionReducer } from '@ngrx/store';

import * as UserActions from './user.actions';
import { IUserState, initialState } from './user.state';

const reducer: ActionReducer<IUserState> = createReducer(
	initialState,

	on(
		UserActions.setLanguageEn,
		(state: IUserState): IUserState => {
			return {
				...state,
				language: 'en',
			};
		}
	),

	on(
		UserActions.setLanguageRu,
		(state: IUserState): IUserState => {
			return {
				...state,
				language: 'ru',
			};
		}
	),

	on(
		UserActions.setThemeLight,
		(state: IUserState): IUserState => {
			return {
				...state,
				theme: 'light',
			};
		}
	),

	on(
		UserActions.setThemeDark,
		(state: IUserState): IUserState => {
			return {
				...state,
				theme: 'dark',
			};
		}
	),

	on(
		UserActions.tooltipShown,
		(state: IUserState): IUserState => {
			return {
				...state,
				showTooltip: false,
			};
		}
	)
);

export function userReducer(
	state: IUserState | undefined,
	action: Action
): IUserState {
	return reducer(state, action);
}
