import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';

import { GooglePlaceDirective } from 'ngx-google-places-autocomplete';

import { GeoCoordinates } from 'src/app/models/geo-oordinates.model';
import { GeoFacade } from 'src/app/store/geo';

import { UserFacade } from '../../store/user/user.facade';
import { Title } from '@angular/platform-browser';

@Component({
	selector: 'app-home-page',
	templateUrl: './home-page.component.html',
	styleUrls: ['./home-page.component.scss'],
})
export class HomePageComponent implements OnInit, OnDestroy {
	private SubSelectGeoCoordinates: Subscription;
	private SubSelectGeoLocation: Subscription;
	private subSelectCurrentLanguage: Subscription;

	public coordinates: GeoCoordinates = {
		latitude: 0,
		longitude: 0,
	};
	public location: string;

	public date: Date;
	public currentLanguage: string;

	@ViewChild('placesRef') public placesRef: GooglePlaceDirective;

	constructor(
		private titleService: Title,
		private geoFacade: GeoFacade,
		private userFacade: UserFacade
	) {}

	public ngOnInit(): void {
		this.titleService.setTitle('Air Around');

		this.SubSelectGeoCoordinates = this.geoFacade
			.selectGeoCoordinates()
			.subscribe((data: GeoCoordinates) => {
				this.coordinates.latitude = data.latitude;
				this.coordinates.longitude = data.longitude;
			});

		this.SubSelectGeoLocation = this.geoFacade
			.selectGeoLocation()
			.subscribe((location: string) => {
				this.location = location;
			});

		this.subSelectCurrentLanguage = this.userFacade
			.selectCurrentLanguage()
			.subscribe((language: string) => {
				this.currentLanguage = language;
			});

		this.date = new Date();
	}

	public ngOnDestroy(): void {
		this.SubSelectGeoCoordinates.unsubscribe();
		this.SubSelectGeoLocation.unsubscribe();
		this.subSelectCurrentLanguage.unsubscribe();
	}

	public onInputChange(address: any): void {
		this.geoFacade.setCoordinates(address);
		this.geoFacade.setLocation(address);
	}
}
