import {
	Component,
	Inject,
	ViewChild,
	ElementRef,
	OnInit,
	OnDestroy,
} from '@angular/core';
import { DOCUMENT, ViewportScroller } from '@angular/common';

import { fromEvent, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';

@Component({
	selector: 'app-info',
	templateUrl: './info.component.html',
	styleUrls: ['./info.component.scss'],
})
export class InfoComponent implements OnInit, OnDestroy {
	private subScroll: Subscription;

	@ViewChild('info') private info: ElementRef;

	constructor(
		@Inject(DOCUMENT) private document: Document,
		private vc: ViewportScroller
	) {}

	private getRem(): number {
		return Number(
			getComputedStyle(document.body, '').fontSize.match(
				/(\d*(\.\d*)?)px/
			)[1]
		);
	}

	public ngOnInit(): void {
		this.subScroll = fromEvent(window, 'scroll')
			.pipe(debounceTime(50), distinctUntilChanged())
			.subscribe(() => {
				if (this.vc.getScrollPosition()[1] > this.getRem() * 98.5) {
					this.info.nativeElement.classList.add('info__active');
				} else {
					this.info.nativeElement.classList.remove('info__active');
				}
			});
	}

	public ngOnDestroy(): void {
		this.subScroll.unsubscribe();
	}
}
