import { Component, OnInit, OnDestroy } from '@angular/core';
import { UserFacade } from 'src/app/store/user';
import { HttpClient } from '@angular/common/http';
import { Subscription } from 'rxjs';
import {
	GeoCoordinates,
	GeoCoordinatesQuery,
} from 'src/app/models/geo-oordinates.model';
import { RouterFacade } from 'src/app/store/router';

@Component({
	selector: 'app-map',
	templateUrl: './map.component.html',
	styleUrls: ['./map.component.scss'],
})
export class MapComponent implements OnInit, OnDestroy {
	private subSelectQueryParams: Subscription;
	private subSelectCurrentTheme: Subscription;

	public coordinates: GeoCoordinates = {
		latitude: 0,
		longitude: 0,
	};
	public styles: any = null;

	constructor(
		private httpClient: HttpClient,
		private routerFacade: RouterFacade,
		private userFacade: UserFacade
	) {}

	public ngOnInit(): void {
		this.subSelectCurrentTheme = this.userFacade
			.selectCurrentTheme()
			.subscribe((theme: string) => {
				this.httpClient
					.get(`../../../assets/map/${theme}/styles.json`)
					.subscribe((styles: {}) => {
						this.styles = styles;
					});
			});

		this.subSelectQueryParams = this.routerFacade
			.selectQueryParams()
			.subscribe((coordinates: GeoCoordinatesQuery) => {
				this.coordinates.latitude = Number(coordinates.ltd);
				this.coordinates.longitude = Number(coordinates.lng);
			});
	}

	public ngOnDestroy(): void {
		this.subSelectCurrentTheme.unsubscribe();
		this.subSelectQueryParams.unsubscribe();
	}
}
