import { createReducer, on, Action, ActionReducer } from '@ngrx/store';

import * as AirActions from './air.actions';
import { IAirState, initialState } from './air.state';
import { AirData } from 'src/app/models/air-data.model';

const reducer: ActionReducer<IAirState> = createReducer(
	initialState,

	on(
		AirActions.getAirDataSuccess,
		(state: IAirState, { airData }: { airData: AirData }): IAirState => {
			return {
				...state,

				aqi: airData.aqi,
				o3: airData.o3,
				co: airData.co,
				no2: airData.no2,
				so2: airData.so2,
				pm10: airData.pm10,
				pm25: airData.pm25,

				isLoading: false,
			};
		}
	),

	on(
		AirActions.getAirDataError,
		(state: IAirState, { error }: { error: any }): IAirState => {
			throw error;
		}
	)
);

export function airReducer(
	state: IAirState | undefined,
	action: Action
): IAirState {
	return reducer(state, action);
}
