import { createAction } from '@ngrx/store';
import { Action } from '../action-type';

export const setLanguageEn: Action<string> = createAction(
	'[User | Language] English language selected.'
);

export const setLanguageRu: Action<string> = createAction(
	'[User | Language] Russian language selected.'
);

export const setThemeLight: Action<string> = createAction(
	'[User | Theme] Light theme selected.'
);

export const setThemeDark: Action<string> = createAction(
	'[User | Theme] Dark theme selected.'
);

export const tooltipShown: Action<string> = createAction(
	'[User | Tooltip ] User has seen a tooltip.'
);
