import { createReducer, on, Action, ActionReducer } from '@ngrx/store';

import * as GeoActions from './geo.actions';
import { IGeoState, initialState } from './geo.state';
import { GeoCoordinates } from 'src/app/models/geo-oordinates.model';

const reducer: ActionReducer<IGeoState> = createReducer(
	initialState,

	on(
		GeoActions.getCoordinatesSuccess,
		(
			state: IGeoState,
			{ coordinates }: { coordinates: GeoCoordinates }
		): IGeoState => {
			return {
				...state,
				isLoading: false,
				oordinates: {
					latitude: coordinates.latitude,
					longitude: coordinates.longitude,
				},
			};
		}
	),

	on(
		GeoActions.getLocationByCoordinatesSuccess,
		(state: IGeoState, { location }: { location: string }): IGeoState => {
			return {
				...state,
				location,
			};
		}
	),

	on(
		GeoActions.setCoordinatesByInputSelectedLocation,
		(
			state: IGeoState,
			{ coordinates }: { coordinates: GeoCoordinates }
		): IGeoState => {
			return {
				...state,
				oordinates: {
					latitude: coordinates.latitude,
					longitude: coordinates.longitude,
				},
			};
		}
	),

	on(
		GeoActions.setLocationByInputSelectedLocation,
		(state: IGeoState, { location }: { location: string }): IGeoState => {
			return {
				...state,
				location,
			};
		}
	),

	on(
		GeoActions.getCoordinatesError,
		(state: IGeoState, { error }: { error: PositionError }): IGeoState => {
			throw error;
		}
	),

	on(
		GeoActions.getLocationByCoordinatesError,
		(state: IGeoState, { error }: { error: any }): IGeoState => {
			throw error;
		}
	)
);

export function geoReducer(
	state: IGeoState | undefined,
	action: Action
): IGeoState {
	return reducer(state, action);
}
