import { Directive, Input, ElementRef, HostListener } from '@angular/core';

@Directive({
	selector: '[parallax]',
})
export class ParallaxDirective {
	// tslint:disable-next-line: no-input-rename
	@Input('ratio') public parallaxRatio: number = 1;
	// tslint:disable-next-line: no-input-rename
	@Input('top') public initialTop: number = 0;

	constructor(private elementRef: ElementRef) {
		this.initialTop = this.elementRef.nativeElement.getBoundingClientRect().top;
	}

	@HostListener('window:scroll', ['$event'])
	public onWindowScroll(): void {
		this.elementRef.nativeElement.style.top =
			(this.initialTop - window.scrollY * this.parallaxRatio) / 10 +
			'rem';
	}
}
