import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
	providedIn: 'root',
})
export class MusicService {
	private totalSongs: number = 19;
	private shuffleArray: number[];

	private volume: BehaviorSubject<number> = new BehaviorSubject(0);
	private audio: BehaviorSubject<number> = new BehaviorSubject(
		this.generateRandomSong()
	);

	private generateRandomSong(): number {
		if (
			this.shuffleArray === undefined ||
			this.shuffleArray?.length === 0
		) {
			this.generateShuffleArray();
		}

		const randomKey: number = Math.round(
			Math.random() * (this.shuffleArray.length - 1)
		);

		const result: number = this.shuffleArray[randomKey];

		this.shuffleArray = this.shuffleArray.filter(
			(n: number, i: number) => i !== randomKey
		);

		return result;
	}

	private generateShuffleArray(): void {
		this.shuffleArray = Array.from(
			{ length: this.totalSongs },
			(v: number, i: number) => i + 1
		);
	}

	public setVolume(volume: number): void {
		this.volume.next(volume);
	}

	public nextSong(): void {
		this.audio.next(this.generateRandomSong());
	}

	public getVolume(): BehaviorSubject<number> {
		return this.volume;
	}

	public getAudio(): BehaviorSubject<number> {
		return this.audio;
	}
}
