import { Injectable } from '@angular/core';
import { Store, select } from '@ngrx/store';

import * as WeatherActions from './weather.actions';
import { selectWeatherData, selectWeatherIcon } from './weather.selectors';
import { Observable } from 'rxjs';
import { IWeatherState } from './weather.state';

@Injectable({
	providedIn: 'root',
})
export class WeatherFacade {
	constructor(private store: Store) {}

	public getWeatherDataByApi(): void {
		this.store.dispatch(WeatherActions.getWeatherDataByApi({}));
	}

	public selectWeatherData(): Observable<IWeatherState> {
		return this.store.pipe(select(selectWeatherData));
	}

	public selectWeatherIcon(): Observable<string> {
		return this.store.pipe(select(selectWeatherIcon));
	}
}
