import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { catchError, map, withLatestFrom, mergeMap } from 'rxjs/operators';

import { Action } from '@ngrx/store';
import { Actions, createEffect, ofType } from '@ngrx/effects';

import * as AirActions from './air.actions';
import { AirService } from '../../services/air.service';
import { WeatherbitAir } from 'src/app/models/weatherbit.model';
import { RouterFacade } from '../router';
import { Params } from '@angular/router';
import { TypedAction } from '@ngrx/store/src/models';

@Injectable()
export class AirEffects {
	public getAirDataByApi$: Observable<Action> = createEffect(() =>
		this.action$.pipe(
			ofType(AirActions.getAirDataByApi),
			withLatestFrom(this.routerFacade.selectQueryParams()),
			mergeMap(
				([, coordinates]: [object & TypedAction<string>, Params]) =>
					this.airService
						.getAirDataByApi(coordinates.ltd, coordinates.lng)
						.pipe(
							map((weatherbitAir: WeatherbitAir) => {
								return AirActions.getAirDataSuccess({
									airData: weatherbitAir.data[0],
								});
							}),
							catchError((error: any) => {
								return of(
									AirActions.getAirDataError({ error })
								);
							})
						)
			)
		)
	);

	constructor(
		private action$: Actions,
		private airService: AirService,
		private routerFacade: RouterFacade
	) {}
}
