import { createReducer, on, Action, ActionReducer } from '@ngrx/store';

import * as WeatherActions from './weather.actions';
import { IWeatherState, initialState } from './weather.state';
import { WeatherData } from '../../models/weather-data.model';

const reducer: ActionReducer<IWeatherState> = createReducer(
	initialState,

	on(
		WeatherActions.getWeatherDataSuccess,
		(
			state: IWeatherState,
			{ weatherData }: { weatherData: WeatherData }
		): IWeatherState => {
			return {
				...state,

				temperature: weatherData.temp,
				apparent_temperature: weatherData.app_temp,
				relative_humidity: weatherData.rh,
				pressure: weatherData.pres,
				ultraviolet: weatherData.uv,
				wind_speed: weatherData.wind_spd,
				weather: {
					icon: weatherData.weather.icon,
					code: weatherData.weather.code,
					description: weatherData.weather.description,
				},
				sunrise: weatherData.sunrise,
				sunset: weatherData.sunset,
				clouds: weatherData.clouds,
				part_of_the_day: weatherData.pod,

				isLoading: false,
			};
		}
	),

	on(
		WeatherActions.getWeatherDataError,
		(state: IWeatherState, { error }: { error: any }): IWeatherState => {
			throw error;
		}
	)
);

export function weatherReducer(
	state: IWeatherState | undefined,
	action: Action
): IWeatherState {
	return reducer(state, action);
}
