import { Component, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, Subscription } from 'rxjs';
import { GeoCoordinates } from 'src/app/models/geo-oordinates.model';
import { GeoFacade } from 'src/app/store/geo';

@Component({
	selector: 'app-analyze-button',
	templateUrl: './analyze-button.component.html',
	styleUrls: ['./analyze-button.component.scss'],
})
export class AnalyzeButtonComponent implements OnDestroy {
	private SubSelectGeoCoordinates: Subscription;

	public coordinates$: Observable<GeoCoordinates>;
	public coordinates: GeoCoordinates = {
		latitude: 0,
		longitude: 0,
	};

	constructor(private router: Router, private geoFacade: GeoFacade) {}

	public proceedToInfoPage(): void {
		this.SubSelectGeoCoordinates = this.geoFacade
			.selectGeoCoordinates()
			.subscribe((data: GeoCoordinates) => {
				this.coordinates.latitude = data.latitude;
				this.coordinates.longitude = data.longitude;
			});

		setTimeout(() => {
			this.router.navigate(['/info'], {
				queryParams: {
					ltd: `${this.coordinates.latitude}`,
					lng: `${this.coordinates.longitude}`,
				},
			});
		}, 0);
	}

	public ngOnDestroy(): void {
		this.SubSelectGeoCoordinates.unsubscribe();
	}
}
