import { ActionReducerMap } from '@ngrx/store';
import * as fromRouter from '@ngrx/router-store';

import { IGeoState, geoReducer } from './geo';
import { IAirState, airReducer } from './air';
import { IWeatherState, weatherReducer } from './weather';
import { IUserState, userReducer } from './user';
import { IRouterState } from './router';

import { GeoEffects } from './geo/geo.effects';
import { AirEffects } from './air/air.effects';
import { WeatherEffects } from './weather/weather.effects';

export const enum FeatureKey {
	Geo = 'Geo',
	Air = 'Air',
	Weather = 'Weather',
	User = 'User',
	Router = 'Router',
}

export interface IAppState {
	[FeatureKey.Geo]: IGeoState;
	[FeatureKey.Air]: IAirState;
	[FeatureKey.Weather]: IWeatherState;
	[FeatureKey.User]: IUserState;
	[FeatureKey.Router]: fromRouter.RouterReducerState<IRouterState>;
}

export const reducers: ActionReducerMap<IAppState> = {
	[FeatureKey.Geo]: geoReducer,
	[FeatureKey.Air]: airReducer,
	[FeatureKey.Weather]: weatherReducer,
	[FeatureKey.User]: userReducer,
	[FeatureKey.Router]: fromRouter.routerReducer,
};

export const effects: Array<
	typeof GeoEffects | typeof AirEffects | typeof WeatherEffects
> = [GeoEffects, AirEffects, WeatherEffects];
