import { createAction, props } from '@ngrx/store';
import { Action } from '../action-type';
import { AirData } from 'src/app/models/air-data.model';

export const getAirDataByApi: Action<string> = createAction(
	'[AIR] Using API to get air data...'
);

export const getAirDataSuccess: Action<
	string,
	{ airData: AirData }
> = createAction(
	'[AIR] Air data requset success!',
	props<{ airData: AirData }>()
);

export const getAirDataError: Action<string, { error: any }> = createAction(
	'[AIR] Air data requset error!',
	props<{ error: any }>()
);
