import { createAction, props } from '@ngrx/store';
import { Action } from '../action-type';
import { WeatherData } from '../../models/weather-data.model';

export const getWeatherDataByApi: Action<string> = createAction(
	'[WEATHER] Using API to get weather data...'
);

export const getWeatherDataSuccess: Action<
	string,
	{ weatherData: WeatherData }
> = createAction(
	'[WEATHER] Weather data requset success!',
	props<{ weatherData: WeatherData }>()
);

export const getWeatherDataError: Action<string, { error: any }> = createAction(
	'[WEATHER] Weather data requset error!',
	props<{ error: any }>()
);
