import { Component, OnInit, OnDestroy, AfterViewInit } from '@angular/core';

import { Howl } from 'howler';
import { Subscription } from 'rxjs';
import { MusicService } from 'src/app/components/music/music.service';

@Component({
	selector: 'app-music',
	template: '',
})
export class MusicComponent implements OnInit, AfterViewInit, OnDestroy {
	private subGetAudio: Subscription;
	private subGetVolume: Subscription;

	private audio: Howl;
	private volume: number = 0;

	constructor(private musicService: MusicService) {}

	public ngOnInit(): void {
		this.subGetAudio = this.musicService
			.getAudio()
			.subscribe((song: number) => {
				this.audio?.stop();

				const musicService: MusicService = this.musicService;

				this.audio = new Howl({
					src: [`../assets/music/${song}.mp3`],
					volume: this.volume,
					onend(): void {
						musicService.nextSong();
					},
				});

				this.audio.play();
			});

		this.subGetVolume = this.musicService
			.getVolume()
			.subscribe((volume: number) => {
				this.audio.volume(volume);
				this.volume = volume;

				volume === 0 ? this.audio.mute(true) : this.audio.mute(false);
			});
	}

	public ngAfterViewInit(): void {
		this.audio.play();
	}

	public ngOnDestroy(): void {
		this.subGetVolume.unsubscribe();
		this.subGetAudio.unsubscribe();
	}
}
